import React from "react";
import clsx from "clsx";
import { makeStyles } from "@material-ui/core/styles";
import Drawer from "@material-ui/core/Drawer";
import Button from "@material-ui/core/Button";
import List from "@material-ui/core/List";
import Divider from "@material-ui/core/Divider";
import ListItem from "@material-ui/core/ListItem";
import ListItemIcon from "@material-ui/core/ListItemIcon";
import ListItemText from "@material-ui/core/ListItemText";
import InboxIcon from "@material-ui/icons/MoveToInbox";
import MailIcon from "@material-ui/icons/Mail";
import MenuIcon from "@material-ui/icons/Menu";
import { NavLink } from "react-router-dom";
import FlashOnIcon from "@material-ui/icons/FlashOn";
import ShoppingCartIcon from "@material-ui/icons/ShoppingCart";
import FavoriteIcon from "@material-ui/icons/Favorite";
import NewReleasesIcon from "@material-ui/icons/NewReleases";
const useStyles = makeStyles({
  list: {
    width: 250,
  },
  fullList: {
    width: "auto",
  },
  menu: {
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
  },
  ln: {
    color: "#3f51b5",
    textDecoration: "none",
  },
});

export default function Panel() {
  const classes = useStyles();
  const [state, setState] = React.useState({
    top: false,
    left: false,
    bottom: false,
    right: false,
  });

  const toggleDrawer = (anchor, open) => (event) => {
    if (
      event.type === "keydown" &&
      (event.key === "Tab" || event.key === "Shift")
    ) {
      return;
    }

    setState({ ...state, [anchor]: open });
  };

  const list = (anchor) => (
    <div
      className={clsx(classes.list, {
        [classes.fullList]: anchor === "top" || anchor === "bottom",
      })}
      role="presentation"
      onClick={toggleDrawer(anchor, false)}
      onKeyDown={toggleDrawer(anchor, false)}
    >
      {/* <List>
        <ListItem button key="Starred">
          <ListItemIcon>
            <MailIcon />
          </ListItemIcon>
          <ListItemText primary="Starred" />
        </ListItem>
      </List>
      <Divider /> */}
      <List>
        <NavLink
          className={classes.ln}
          to="/HarryPotter"
          activeClassName={"ln_active"}
        >
          <ListItem button key="Starred">
            <ListItemIcon>
              <FlashOnIcon color="primary"></FlashOnIcon>
            </ListItemIcon>
            <ListItemText primary="Harry Potter" />
          </ListItem>
        </NavLink>
        <Divider />
        {/* fddf */}
        <NavLink className={classes.ln} to="/New" activeClassName={"ln_active"}>
          <ListItem button key="Starred">
            <ListItemIcon>
              <NewReleasesIcon color="primary"></NewReleasesIcon>
            </ListItemIcon>
            <ListItemText primary=" Новинки" />
          </ListItem>
        </NavLink>
        <Divider />
        {/* fddf */}
        <NavLink className={classes.ln} to="/Top" activeClassName={"ln_active"}>
          <ListItem button key="Starred">
            <ListItemIcon>
              <FavoriteIcon color="secondary"></FavoriteIcon>
            </ListItemIcon>
            <ListItemText color="secondary" primary="Топ книг" />
          </ListItem>
        </NavLink>
        <Divider />
        {/* fddf */}
        <NavLink
          className={classes.ln}
          to="/Cart"
          activeClassName={"ln_active"}
        >
          <ListItem button key="Starred">
            <ListItemIcon>
              <ShoppingCartIcon color="primary"></ShoppingCartIcon>
            </ListItemIcon>
            <ListItemText primary="Go to cart" />
          </ListItem>
        </NavLink>
        <Divider />
        {/* fddf */}
      </List>
    </div>
  );

  return (
    <div className={classes.menu}>
      <MenuIcon onClick={toggleDrawer("left", true)} />
      <Drawer
        anchor={"left"}
        open={state["left"]}
        onClose={toggleDrawer("left", false)}
      >
        {list("left")}
      </Drawer>
    </div>
  );
}
